import { Component, OnInit } from "@angular/core";
import { dateDiff } from "src/app/helpers/DateTimeUtil";
import { Hires } from "src/app/models/Hires.model";
import { IPager } from "src/app/models/IPager.model";
import { HireService } from "src/app/services/hire.service";
import * as moment from "moment";
import { NotificationService } from "src/app/services/notification.service";

@Component({
  selector: "app-admin-hires",
  templateUrl: "./admin-hires.component.html",
  styleUrls: ["./admin-hires.component.css"],
})
export class AdminHiresComponent implements OnInit {
  private hasMore: boolean;
  private hires: Hires[] = [];
  private pager: IPager;
  private searchquery: string;
  private reference: string;
  private selectedHire: Hires;

  constructor(
    private hireService: HireService,
    private notifyService: NotificationService
  ) {}

  ngOnInit() {
    this.reference = `ref-${Math.ceil(Math.random() * 10e13)}`;
    this.loadHires();
  }

  loadHires(page: number = 1, searchquery = "") {
    if (this.pager && page > this.pager.totalPages) return;

    this.hireService.getHires(page, searchquery).subscribe(
      (res) => {
        if (res.success) {
          this.pager = res.data;
          this.hires = [...this.hires, ...res.data.results];
          console.log(this.pager);
          this.hasMore =
            Number(this.pager.page) < Number(this.pager.totalPages);
          this.pager.page = Number(this.pager.page);
        }
      },
      (reason) => {
        console.log(reason);
      }
    );
  }
  calculateDiff(date: string) {
    return moment(Date.now()).diff(date, "days");
  }

  onCompleteHire(hireId: number) {
    this.hireService.completeHire({ id: hireId }).subscribe(
      (res) => {
        if (res.success) {
          this.hires = [];
          this.loadHires();
        }
      },
      (reason) => {
        console.log(reason);
        this.notifyService.showError(
          "We were unable to process your request",
          "Operation failed"
        );
      }
    );
  }
  onSearch() {
    this.hires = [];
    this.loadHires(1, this.searchquery);
  }

  paymentCancel() {
    console.log("canceled");
  }

  paymentDone(paymentInfo: any) {
    console.log(paymentInfo);
    console.log(this.selectedHire);
    let payment = {
      hireId: this.selectedHire.id,
      paymentRef: paymentInfo.reference,
      amountPaid:
        this.selectedHire.Vehicle.hireCost *
        this.calculateDiff(this.selectedHire.returnedAt),
    };

    this.hireService.processOverduePayment(payment).subscribe(
      (res) => {
        if (res.success) {
          this.hires = [];
          this.loadHires(this.pager.page);
        }
      },
      (reason) => {
        console.log(reason);
        this.notifyService.showError(
          "We were unable to process your request",
          "Operation failed."
        );
      }
    );
  }

  onSelectHire(hire: Hires) {
    this.selectedHire = hire;
  }
}
