import { Component, OnInit } from "@angular/core";
import { AuthService } from "../services/auth.service";

@Component({
  selector: "app-login",
  templateUrl: "./login.component.html",
  styleUrls: ["./login.component.css"],
})
export class LoginComponent implements OnInit {
  private loginCred: {
    emailAddress: string;
    password: string;
    userType: string;
  };
  private isContactingServer = false;
  constructor(private authService: AuthService) {
    this.loginCred = { emailAddress: "", password: "", userType: "customer" };
  }

  ngOnInit() {}

  onLogin() {
    this.isContactingServer = true;
    let redirectUrl =
      this.loginCred.userType === "customer" ? "/cars" : "/admin/dashboard/";
    this.authService.login(redirectUrl, this.loginCred, () => {
      this.isContactingServer = false;
    });
  }
}
