import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { Customer } from "../models/Customers.model";
import { CoreService } from "./core.service";

@Injectable({
  providedIn: "root",
})
export class CustomerService {
  private apiUrl = "api/customers/";
  constructor(private coreService: CoreService) {}

  getCustomers(
    page: number = 1,
    searchquery: string = ""
  ): Observable<IFeedBack> {
    return this.coreService.getData(
      `${this.apiUrl}?page=${page}&searchquery=${searchquery}`
    );
  }

  saveCustomer(customer: Customer): Observable<IFeedBack> {
    return this.coreService.postData(customer, this.apiUrl);
  }

  updateCustomer(customer: Customer): Observable<IFeedBack> {
    return this.coreService.putData(customer, this.apiUrl);
  }
}
