import { Component, OnInit } from "@angular/core";
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { IPager } from "../models/IPager.model";
import { Vehicle } from "../models/Vehicle.model";
import { NotificationService } from "../services/notification.service";
import { VehicleService } from "../services/vehicle.service";
import { VehicleProfileModalComponent } from "../vehicle-profile-modal/vehicle-profile-modal.component";

@Component({
  selector: "app-vehicle-listing",
  templateUrl: "./vehicle-listing.component.html",
  styleUrls: ["./vehicle-listing.component.css"],
})
export class VehicleListingComponent implements OnInit {
  private vehicles: Vehicle[] = [];
  private pager: IPager;
  private hasMore: boolean;
  private searchquery: string;

  private isContactingServer = false;

  constructor(
    private vehicleService: VehicleService,
    private modalService: NgbModal,
    private notifyService: NotificationService
  ) {}

  ngOnInit() {
    this.loadVehicles();
  }

  loadVehicles(page: number = 1, searchquery = "") {
    if (this.pager && page > this.pager.totalPages) return;
    this.isContactingServer = true;
    this.vehicleService.getVehicles(page, searchquery).subscribe(
      (res) => {
        this.isContactingServer = false;
        if (res.success) {
          this.pager = res.data;
          this.vehicles = [...this.vehicles, ...res.data.results];
          if (!searchquery) {
            this.hasMore =
              Number(this.pager.page) < Number(this.pager.totalPages);
          }
        }
      },
      (reason) => {
        console.log(reason);
        this.isContactingServer = false;
        this.notifyService.showError(
          "We encountered a problem while loading data",
          "Operation failed."
        );
      }
    );
  }

  onViewVehicleProfile(vehicle: Vehicle) {
    let modalRef = this.modalService.open(VehicleProfileModalComponent);
    modalRef.componentInstance.vehicle = vehicle;
  }

  onSearch() {
    this.hasMore = false;
    this.vehicles = [];
    this.loadVehicles(0, this.searchquery);
  }
}
