import { Component, OnInit } from "@angular/core";
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { ImageSnippet } from "src/app/models/ImageSnippet.model";
import { IPager } from "src/app/models/IPager.model";
import { Vehicle } from "src/app/models/Vehicle.model";
import { VehicleType } from "src/app/models/VehicleType.model";
import { VehicleService } from "src/app/services/vehicle.service";
import { VehicleProfileModalComponent } from "src/app/vehicle-profile-modal/vehicle-profile-modal.component";
import { isNull } from "util";
import { AdminVehicleModalComponent } from "../admin-vehicle-modal/admin-vehicle-modal.component";

@Component({
  selector: "app-admin-vehicles",
  templateUrl: "./admin-vehicles.component.html",
  styleUrls: ["./admin-vehicles.component.css"],
})
export class AdminVehiclesComponent implements OnInit {
  private vehicles: Vehicle[] = [];
  private pager: IPager;
  private hasMore: boolean;
  private searchquery: string;

  constructor(
    private modalService: NgbModal,
    private vehicleService: VehicleService
  ) {}

  ngOnInit() {
    this.loadVehicles();
  }

  loadVehicles(page: number = 1, searchquery = "") {
    if (this.pager && page > this.pager.totalPages) return;

    this.vehicleService.getVehicles(page, searchquery).subscribe(
      (res) => {
        if (res.success) {
          this.pager = res.data;
          this.vehicles = [...this.vehicles, ...res.data.results];
          console.log(this.pager);
          this.hasMore =
            Number(this.pager.page) < Number(this.pager.totalPages);
          this.pager.page = Number(this.pager.page);
        }
      },
      (reason) => {
        console.log(reason);
      }
    );
  }

  openModal(vehicle: Vehicle = null) {
    let modalRef = this.modalService.open(AdminVehicleModalComponent);
    modalRef.componentInstance.title = "New Car";
    modalRef.componentInstance.isNewVehicle = isNull(vehicle);
    if (!isNull(vehicle)) {
      modalRef.componentInstance.vehicle = vehicle;
      modalRef.componentInstance.title = "Edit Car";
    }

    modalRef.result
      .then((action) => {
        console.log(action);
        if (action === "saved") {
          console.log("here");
          this.vehicles = [];
          this.loadVehicles();
        }
      })
      .catch((reason) => {
        console.log(reason);
      });
  }

  onViewVehicleProfile(vehicle: Vehicle) {
    let modalRef = this.modalService.open(VehicleProfileModalComponent);
    modalRef.componentInstance.vehicle = vehicle;
    modalRef.componentInstance.isPreview = true;
  }

  onSearch() {
    this.vehicles = [];
    this.loadVehicles(1, this.searchquery);
  }
}
