import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { CoreService } from "./core.service";

@Injectable({
  providedIn: "root",
})
export class HireService {
  private apiUrl = "api/hires/";
  constructor(private coreService: CoreService) {}

  getHires(page = 1, searchquery = "", customerId = ""): Observable<IFeedBack> {
    return this.coreService.getData(
      `${this.apiUrl}?page=${page}&searchquery=${searchquery}&cid=${customerId}`
    );
  }

  completeHire(data: { id: number }): Observable<IFeedBack> {
    return this.coreService.postData(data, `${this.apiUrl}complete`);
  }

  processHire(data: IHire): Observable<IFeedBack> {
    return this.coreService.postData(data, this.apiUrl);
  }

  processOverduePayment(data: {
    hireId: number;
    paymentRef: string;
    amountPaid: number;
  }): Observable<IFeedBack> {
    return this.coreService.postData(data, `${this.apiUrl}overdue`);
  }
}
