import { Component, OnInit } from "@angular/core";
import { NgbActiveModal } from "@ng-bootstrap/ng-bootstrap";
import { Admin } from "src/app/models/Admin.model";
import { AdminService } from "src/app/services/admin.service";
import { NotificationService } from "src/app/services/notification.service";

@Component({
  selector: "app-admin-edit-email-modal",
  templateUrl: "./admin-edit-email-modal.component.html",
  styleUrls: ["./admin-edit-email-modal.component.css"],
})
export class AdminEditEmailModalComponent implements OnInit {
  private admin: Admin;
  private isContactingServer: boolean;
  constructor(
    private activeModal: NgbActiveModal,
    private adminService: AdminService,
    private notifyService: NotificationService
  ) {}

  ngOnInit() {}

  onUpdateEmail() {
    console.log(this.admin);
    this.isContactingServer = true;
    this.adminService.updateAdmin(this.admin).subscribe(
      (res) => {
        this.isContactingServer = false;
        if (res.success) {
          this.notifyService.showSuccess(
            "Email updated successfully",
            "Success"
          );
          this.activeModal.close("close");
        } else {
          this.notifyService.showError(
            "We were unable to process your request.",
            "Operation failed"
          );
        }
      },
      (reason) => {
        this.isContactingServer = false;
        this.notifyService.showError(
          "We were unable to process your request.",
          "Operation failed"
        );
      }
    );
  }
}
