import { Component, OnInit } from "@angular/core";
import { CommonService } from "../services/common.service";
import { NotificationService } from "../services/notification.service";

@Component({
  selector: "app-contact-us",
  templateUrl: "./contact-us.component.html",
  styleUrls: ["./contact-us.component.css"],
})
export class ContactUsComponent implements OnInit {
  private composer: IMail;
  private isContactingServer = false;
  constructor(
    private notifyService: NotificationService,
    private commonService: CommonService
  ) {
    this.composer = { name: "", emailAddress: "", message: "" };
  }

  ngOnInit() {}

  onSendMail() {
    this.isContactingServer = true;
    this.commonService.sendMail(this.composer).subscribe(
      (res) => {
        this.isContactingServer = false;
        if (res.success) {
          this.notifyService.showSuccess("Message sent", "Success");
          this.composer.emailAddress = null;
          this.composer.message = null;
          this.composer.name = null;
        } else {
          this.notifyService.showError(
            "We were unable to process your request.",
            "Operation failed."
          );
        }
      },
      (reason) => {
        this.isContactingServer = false;
        console.log(reason);
        this.notifyService.showError(
          "We were unable to process your request.",
          "Operation failed."
        );
      }
    );
  }
}
