import { Component } from "@angular/core";
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { AdminChangePasswordModalComponent } from "./admin/admin-change-password-modal/admin-change-password-modal.component";
import { AdminEditEmailModalComponent } from "./admin/admin-edit-email-modal/admin-edit-email-modal.component";
import { AuthService } from "./services/auth.service";

@Component({
  selector: "app-root",
  templateUrl: "./app.component.html",
  styleUrls: ["./app.component.css"],
})
export class AppComponent {
  title = "Online Car Rentals";
  private userStatus: string;
  private userType: string;

  constructor(
    private authService: AuthService,
    private modalService: NgbModal
  ) {}

  ngOnInit() {
    this.authService.userStatusChanges.subscribe((x) => (this.userStatus = x));
  }

  ngAfterContentChecked(): void {
    //Called after every check of the component's or directive's content.
    //Add 'implements AfterContentChecked' to the class.
    this.authService.getStatusOnRefresh();
    this.userType = this.authService.getUserType();
  }

  logOut() {
    this.authService.logout();
  }

  onEditAdminEmail() {
    let modalRef = this.modalService.open(AdminEditEmailModalComponent, {
      size: "sm",
    });
    modalRef.componentInstance.isContactingServer = true;
    this.authService.getCurrentUser().subscribe((res) => {
      modalRef.componentInstance.isContactingServer = false;
      if (res.success) {
        modalRef.componentInstance.admin = res.data;
      }
    });

    modalRef.result.then(() => {}).catch(() => {});
  }

  onChangeAdminPassword() {
    let modalRef = this.modalService.open(AdminChangePasswordModalComponent, {
      size: "sm",
    });
    modalRef.result.then(() => {}).catch(() => {});
  }
}
