import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { Vehicle } from "../models/Vehicle.model";
import * as moment from "moment";
import { VehicleService } from "../services/vehicle.service";
import { NotificationService } from "../services/notification.service";
import { Customer } from "../models/Customers.model";
import { AuthService } from "../services/auth.service";
import { HireService } from "../services/hire.service";

@Component({
  selector: "app-checkout",
  templateUrl: "./checkout.component.html",
  styleUrls: ["./checkout.component.css"],
})
export class CheckoutComponent implements OnInit {
  private isContactingServer = false;
  private vehicleId: number;
  private hireId: number;
  private vehicle: Vehicle;
  private hireData: IHire;
  private totalDays: number = 0;
  private canMakePayment = false;
  private reference: string;
  private currentUser: Customer;

  constructor(
    private activatedRoute: ActivatedRoute,
    private vehicleService: VehicleService,
    private notifyService: NotificationService,
    private authService: AuthService,
    private router: Router,
    private hireService: HireService
  ) {
    this.hireData = {
      hiredAt: moment({}).format("Y/MM/DD"),
      returnedAt: "",
      vehicleId: null,
      hireId: null,
      status: "active",
      amountPaid: 0,
    };
  }

  ngOnInit() {
    this.hireId = Number(this.activatedRoute.snapshot.params.hid);
    this.vehicleId = Number(this.activatedRoute.snapshot.params.vid);
    this.hireData.vehicleId = this.vehicleId;
    this.hireData.hireId = this.hireId;
    this.reference = `ref-${Math.ceil(Math.random() * 10e13)}`;
    this.isContactingServer = true;
    if (!isNaN(this.vehicleId)) {
      this.vehicleService.getVehicle(this.vehicleId).subscribe(
        (res) => {
          if (res.success) {
            this.vehicle = res.data;

            this.authService.getCurrentUser().subscribe(
              (res) => {
                this.isContactingServer = false;
                if (res.success) {
                  this.currentUser = res.data;
                } else {
                  this.notifyService.showError(
                    "Failed to load your data",
                    "Operation Failed"
                  );
                }
              },
              (reason) => {
                this.isContactingServer = false;
              }
            );
          }
        },
        (reason) => {
          console.log(reason);
          this.notifyService.showError(
            "We were unable to load data",
            "operation failed"
          );
        }
      );
    }
  }

  onDateChange() {
    console.log(this.hireData.returnedAt);
    let date =
      this.hireData.returnedAt["year"] +
      "-" +
      this.hireData.returnedAt["month"] +
      "-" +
      this.hireData.returnedAt["day"];
    this.totalDays = moment(date).diff(this.hireData.hiredAt, "days");
    if (this.totalDays < 0) {
      this.canMakePayment = false;
      this.totalDays = 0;
      this.hireData.amountPaid = 0;
      this.notifyService.showWarning(
        "Please select a current or future date",
        "Warning"
      );
      return;
    }
    this.canMakePayment = true;
    this.hireData.amountPaid = this.totalDays * Number(this.vehicle.hireCost);
  }

  paymentCancel() {
    console.log("canceled");
  }

  paymentDone(paymentInfo: any) {
    this.hireData.paymentRef = paymentInfo["reference"];
    this.hireData.customerId = this.currentUser.id;
    this.hireData.returnedAt = `${this.hireData.returnedAt["year"]}/${this.hireData.returnedAt["month"]}/${this.hireData.returnedAt["day"]}`;
    console.log(this.hireData);

    this.hireService.processHire(this.hireData).subscribe(
      (res) => {
        if (res.success) {
          this.notifyService.showSuccess("Payment was successful", "Success");
          this.router.navigate(["/customer/hires/"]);
        } else {
          this.notifyService.showError(
            "We were unable to process your request",
            "Operation failed"
          );
        }
      },
      (reason) => {
        console.log(reason);
        this.notifyService.showError(
          "We were unable to process your request",
          "Operation failed"
        );
      }
    );
  }
}
