import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { Customer } from "../models/Customers.model";
import { CustomerService } from "../services/customer.service";
import { NotificationService } from "../services/notification.service";

@Component({
  selector: "app-signup",
  templateUrl: "./signup.component.html",
  styleUrls: ["./signup.component.css"],
})
export class SignupComponent implements OnInit {
  private userProfile: Customer;
  private isContactingServer: boolean;

  constructor(
    private customerService: CustomerService,
    private notifyService: NotificationService,
    private router: Router
  ) {
    this.userProfile = new Customer(null, "", "", "", "", "", "male");
  }

  ngOnInit() {}

  onSubmit() {
    this.isContactingServer = true;
    this.customerService.saveCustomer(this.userProfile).subscribe(
      (res) => {
        this.isContactingServer = false;
        if (res.success) {
          this.notifyService.showSuccess(
            "Account created successfully",
            "Success"
          );
          this.router.navigate(["/login"]);
        } else {
          this.notifyService.showError(res.message, "Operation failed");
        }
      },
      (reason) => {
        this.notifyService.showError(
          "We were unable to process your request",
          "Operation failed"
        );
      }
    );
  }
}
