import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { AdminCustomersComponent } from "./admin/admin-customers/admin-customers.component";
import { AdminDashboardComponent } from "./admin/admin-dashboard/admin-dashboard.component";
import { AdminHiresComponent } from "./admin/admin-hires/admin-hires.component";
import { AdminTransactionsComponent } from "./admin/admin-transactions/admin-transactions.component";
import { AdminVehiclesComponent } from "./admin/admin-vehicles/admin-vehicles.component";
import { AppComponent } from "./app.component";
import { AuthGuard } from "./auth.guard";
import { CheckoutComponent } from "./checkout/checkout.component";
import { ContactUsComponent } from "./contact-us/contact-us.component";
import { CustomerAccountComponent } from "./customer/customer-account/customer-account.component";
import { CustomerHiresComponent } from "./customer/customer-hires/customer-hires.component";
import { HomepageComponent } from "./homepage/homepage.component";
import { LoginComponent } from "./login/login.component";
import { SignupComponent } from "./signup/signup.component";
import { VehicleListingComponent } from "./vehicle-listing/vehicle-listing.component";

const routes: Routes = [
  { path: "", component: HomepageComponent },
  { path: "home", component: HomepageComponent },
  { path: "contact", component: ContactUsComponent },
  { path: "login", component: LoginComponent },
  { path: "signup", component: SignupComponent },
  { path: "cars", component: VehicleListingComponent },
  {
    path: "checkout/:vid/:hid",
    canActivate: [AuthGuard],
    component: CheckoutComponent,
  },

  {
    path: "customer/profile",
    canActivate: [AuthGuard],
    component: CustomerAccountComponent,
  },
  {
    path: "customer/hires",
    canActivate: [AuthGuard],
    component: CustomerHiresComponent,
  },

  {
    path: "admin/dashboard",
    canActivate: [AuthGuard],
    component: AdminDashboardComponent,
  },
  {
    path: "admin/cars",
    canActivate: [AuthGuard],
    component: AdminVehiclesComponent,
  },
  {
    path: "admin/rentals",
    canActivate: [AuthGuard],
    component: AdminHiresComponent,
  },
  {
    path: "admin/transactions",
    canActivate: [AuthGuard],
    component: AdminTransactionsComponent,
  },
  {
    path: "admin/customers",
    canActivate: [AuthGuard],
    component: AdminCustomersComponent,
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule {}
