import { Component, OnInit } from "@angular/core";
import { Customer } from "src/app/models/Customers.model";
import { AuthService } from "src/app/services/auth.service";
import { CustomerService } from "src/app/services/customer.service";
import { NotificationService } from "src/app/services/notification.service";

@Component({
  selector: "app-customer-account",
  templateUrl: "./customer-account.component.html",
  styleUrls: ["./customer-account.component.css"],
})
export class CustomerAccountComponent implements OnInit {
  private userProfile: Customer;
  private isContactingServer: boolean;
  private isResetingPassword: boolean;
  private userPassword: { password: string; confirmPwd: string };
  constructor(
    private customerService: CustomerService,
    private notifyService: NotificationService,
    private authService: AuthService
  ) {
    this.userProfile = new Customer(null, "", "", "", "", "", "");
    this.userPassword = { password: "", confirmPwd: "" };
  }

  ngOnInit() {
    this.isContactingServer = true;
    this.authService.getCurrentUser().subscribe(
      (res) => {
        this.isContactingServer = false;
        if (res.success) {
          this.userProfile = res.data;
        } else {
          this.notifyService.showError(
            "Failed to load your profile",
            "Operation failed"
          );
        }
      },
      (reason) => {
        this.isContactingServer = false;
        this.notifyService.showError(
          "We were unable to process your request",
          "Operation failed"
        );
      }
    );
  }

  onUpdate() {
    this.isContactingServer = true;
    this.customerService.updateCustomer(this.userProfile).subscribe(
      (res) => {
        this.isContactingServer = false;
        if (res.success) {
          this.notifyService.showSuccess(
            "Profile updated successfully",
            "Success"
          );
        } else {
          this.notifyService.showError(
            "We were unable to process your request",
            "Operation failed"
          );
        }
      },
      (reason) => {
        this.isContactingServer = false;
        this.notifyService.showError(
          "We were unable to process your request",
          "Operation failed"
        );
      }
    );
  }

  onChangePassword() {
    this.isResetingPassword = true;
    this.authService.changePassword(this.userPassword).subscribe(
      (res) => {
        this.isResetingPassword = false;
        if (res.success) {
          this.notifyService.showSuccess(
            "Passsword changed successfully",
            "Success"
          );
          this.authService.logout();
        } else {
          this.notifyService.showError(res.message, "operation failed");
        }
      },
      (reason) => {
        this.isResetingPassword = false;
        this.notifyService.showError(
          "We were unable to process request",
          "operation failed"
        );
      }
    );
  }
}
