import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { Vehicle } from "../models/Vehicle.model";
import { CoreService } from "./core.service";

@Injectable({
  providedIn: "root",
})
export class VehicleService {
  private apiUrl = "api/vehicles";
  constructor(private coreService: CoreService) {}

  getVehicles(
    page: number = 1,
    searchquery: string = ""
  ): Observable<IFeedBack> {
    return this.coreService.getData(
      `${this.apiUrl}?page=${page}&searchquery=${searchquery}`
    );
  }

  getVehicle(id: number): Observable<IFeedBack> {
    return this.coreService.getData(`${this.apiUrl}/${id}`);
  }

  saveVehicles(vehicle: Vehicle): Observable<IFeedBack> {
    return this.coreService.postData(vehicle, this.apiUrl);
  }

  updateVehicle(vehicle: Vehicle): Observable<IFeedBack> {
    return this.coreService.putData(vehicle, this.apiUrl);
  }

  deleteVehicle(id: number): Observable<IFeedBack> {
    return this.coreService.deleteData(id, this.apiUrl);
  }
}
