import { Component, OnInit } from "@angular/core";
import { NgbActiveModal } from "@ng-bootstrap/ng-bootstrap";
import { AuthService } from "src/app/services/auth.service";
import { NotificationService } from "src/app/services/notification.service";

@Component({
  selector: "app-admin-change-password-modal",
  templateUrl: "./admin-change-password-modal.component.html",
  styleUrls: ["./admin-change-password-modal.component.css"],
})
export class AdminChangePasswordModalComponent implements OnInit {
  private userPassword = { password: "", confirmPwd: "" };
  private isContactingServer = false;
  constructor(
    private activeModal: NgbActiveModal,
    private authService: AuthService,
    private notifyService: NotificationService
  ) {}

  ngOnInit() {}

  onChangePassword() {
    console.log(this.userPassword);
    this.isContactingServer = true;
    this.authService.changePassword(this.userPassword).subscribe(
      (res) => {
        this.isContactingServer = false;
        if (res.success) {
          this.notifyService.showSuccess(
            "Password changed successfully",
            "Success"
          );
          this.authService.logout();
          this.activeModal.close("close");
        } else {
          this.notifyService.showError(
            "We were unable to process your request.",
            "Operation failed"
          );
        }
      },
      (reason) => {
        this.notifyService.showError(
          "We were unable to process your request.",
          "Operation failed"
        );
      }
    );
  }
}
