import { Component, OnInit } from "@angular/core";
import { JwtHelperService } from "@auth0/angular-jwt";
import { Hires } from "src/app/models/Hires.model";
import { IPager } from "src/app/models/IPager.model";
import { AuthService } from "src/app/services/auth.service";
import { HireService } from "src/app/services/hire.service";
import * as moment from "moment";
import { Vehicle } from "src/app/models/Vehicle.model";
import { VehicleProfileModalComponent } from "src/app/vehicle-profile-modal/vehicle-profile-modal.component";
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";

@Component({
  selector: "app-customer-hires",
  templateUrl: "./customer-hires.component.html",
  styleUrls: ["./customer-hires.component.css"],
})
export class CustomerHiresComponent implements OnInit {
  private hires: Hires[] = [];
  private hasMore: boolean = false;
  private pager: IPager;
  private userStatus: string;
  private customerId: any;

  constructor(
    private hireService: HireService,
    private authService: AuthService,
    private jwtService: JwtHelperService,
    private modalService: NgbModal
  ) {}

  ngOnInit() {
    this.authService.userStatusChanges.subscribe((x) => (this.userStatus = x));
    let decoded = this.jwtService.decodeToken(this.userStatus);
    this.customerId = decoded["user"]["id"];

    this.loadHires();
  }

  loadHires(page: number = 1, searchquery = "") {
    if (this.pager && page > this.pager.totalPages) return;

    this.hireService.getHires(page, searchquery, this.customerId).subscribe(
      (res) => {
        if (res.success) {
          this.pager = res.data;
          this.hires = [...this.hires, ...res.data.results];
          console.log(this.pager);
          this.hasMore =
            Number(this.pager.page) < Number(this.pager.totalPages);
        }
      },
      (reason) => {
        console.log(reason);
      }
    );
  }

  calculateDiff(date: string) {
    return moment(Date.now()).diff(date, "days");
  }

  onViewVehicleProfile(vehicle: Vehicle) {
    console.log(vehicle);
    let modalRef = this.modalService.open(VehicleProfileModalComponent);
    modalRef.componentInstance.vehicle = vehicle;
    modalRef.componentInstance.isPreview = true;
  }
}
