import { Component, OnInit } from "@angular/core";
import { NgbActiveModal } from "@ng-bootstrap/ng-bootstrap";
import { ImageSnippet } from "src/app/models/ImageSnippet.model";
import { Vehicle } from "src/app/models/Vehicle.model";
import { VehicleManufacturer } from "src/app/models/VehicleManufacturer";
import { VehicleType } from "src/app/models/VehicleType.model";
import { NotificationService } from "src/app/services/notification.service";
import { VehicleManufacturerService } from "src/app/services/vehicle-manufacturer.service";
import { VehicleTypeService } from "src/app/services/vehicle-type.service";
import { VehicleService } from "src/app/services/vehicle.service";

@Component({
  selector: "app-admin-vehicle-modal",
  templateUrl: "./admin-vehicle-modal.component.html",
  styleUrls: ["./admin-vehicle-modal.component.css"],
})
export class AdminVehicleModalComponent implements OnInit {
  private selectedFile: ImageSnippet;
  private vehicle: Vehicle;
  private vehicleTypes: VehicleType[];
  private manufacturers: VehicleManufacturer[];

  constructor(
    private activeModal: NgbActiveModal,
    private vehicleTypeService: VehicleTypeService,
    private manufacturerService: VehicleManufacturerService,
    private vehicleService: VehicleService,
    private notifyService: NotificationService
  ) {
    this.vehicle = new Vehicle(null, "", null, null, null, null, "");
  }

  ngOnInit() {
    this.vehicleTypeService.getVehicleTypes().subscribe(
      (res) => {
        if (res.success) {
          this.vehicleTypes = res.data;
          if (this.vehicleTypes.length > 0) {
            this.vehicle.vehicleTypeId =
              this.vehicle.vehicleTypeId || this.vehicleTypes[0].id;
          }
        }
      },
      (reason) => {
        console.log(reason);
      }
    );

    this.manufacturerService.getManufacturers().subscribe(
      (res) => {
        console.log(res);
        if (res.success) {
          this.manufacturers = res.data;
          if (this.manufacturers.length > 0) {
            this.vehicle.vehicleManufacturerId =
              this.vehicle.vehicleManufacturerId || this.manufacturers[0].id;
          }
        }
      },
      (reason) => {
        console.log(reason);
      }
    );
  }

  onFileChanged(imageInput) {
    let file: File = imageInput.files[0];
    let reader: FileReader = new FileReader();

    reader.addEventListener("load", (event) => {
      this.selectedFile = new ImageSnippet(event.target["result"], file);
      if (!this.selectedFile.hasSizeError) {
        this.vehicle.image = this.selectedFile.src;
      }
    });
    reader.readAsDataURL(file);
  }

  onSave() {
    this.vehicleService.saveVehicles(this.vehicle).subscribe(
      (res) => {
        if (res.success) {
          this.activeModal.close("saved");
          this.notifyService.showSuccess("saved successfully", "Success");
        } else {
          this.notifyService.showError(res.message, "Operation Failed");
        }
      },
      (reason) => {
        console.log(reason);
        this.notifyService.showError(
          "We were unable to process request",
          "Operation Failed"
        );
      }
    );
  }
  onUpdate() {
    this.vehicleService.updateVehicle(this.vehicle).subscribe(
      (res) => {
        if (res.success) {
          this.activeModal.close("updated");
          this.notifyService.showSuccess("updated successfully", "Success");
        } else {
          this.notifyService.showError(res.message, "Operation Failed");
        }
      },
      (reason) => {
        console.log(reason);
        this.notifyService.showError(
          "We were unable to process request",
          "Operation Failed"
        );
      }
    );
  }

}
