import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { CoreService } from "./core.service";

@Injectable({
  providedIn: "root",
})
export class ActivityNotificationsService {
  private apiUrl = "api/notifications";
  constructor(private coreService: CoreService) {}

  getNotifications(): Observable<IFeedBack> {
    return this.coreService.getData(this.apiUrl);
  }
  deleteNotification(id: number): Observable<IFeedBack> {
    return this.coreService.deleteData(id, this.apiUrl);
  }
}
