import { Component, OnInit } from "@angular/core";
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { CustomerProfileComponent } from "src/app/customer-profile/customer-profile.component";
import { Customer } from "src/app/models/Customers.model";
import { IPager } from "src/app/models/IPager.model";
import { CustomerService } from "src/app/services/customer.service";
import { NotificationService } from "src/app/services/notification.service";

@Component({
  selector: "app-admin-customers",
  templateUrl: "./admin-customers.component.html",
  styleUrls: ["./admin-customers.component.css"],
})
export class AdminCustomersComponent implements OnInit {
  private hasMore: boolean;
  private pager: IPager;
  private customers: Customer[] = [];
  private searchquery: string;
  constructor(
    private customerService: CustomerService,
    private notifyService: NotificationService,
    private modalService: NgbModal
  ) {}

  ngOnInit() {
    this.loadCustomers();
  }

  loadCustomers(page: number = 1, searchquery = "") {
    if (this.pager && page > this.pager.totalPages) return;

    this.customerService.getCustomers(page, searchquery).subscribe(
      (res) => {
        if (res.success) {
          this.pager = res.data;
          this.customers = [...this.customers, ...res.data.results];
          console.log(this.pager);
          this.hasMore =
            Number(this.pager.page) < Number(this.pager.totalPages);
        }
      },
      (reason) => {
        console.log(reason);
      }
    );
  }

  onSearch() {
    this.customers = [];
    this.loadCustomers(1, this.searchquery);
  }

  onViewProfile(customer: Customer) {
    let modalRef = this.modalService.open(CustomerProfileComponent);
    modalRef.componentInstance.customer = customer;
  }
}
