import { Component, OnInit } from "@angular/core";
import { ActivityNotificationsService } from "src/app/services/activity-notifications.service";
import { AdminService } from "src/app/services/admin.service";
import { NotificationService } from "src/app/services/notification.service";

@Component({
  selector: "app-admin-dashboard",
  templateUrl: "./admin-dashboard.component.html",
  styleUrls: ["./admin-dashboard.component.css"],
})
export class AdminDashboardComponent implements OnInit {
  private summary;
  private activities = [];
  constructor(
    private adminService: AdminService,
    private activityNotify: ActivityNotificationsService,
    private notifyService: NotificationService
  ) {}

  ngOnInit() {
    this.adminService.getDashboardSummary().subscribe((res) => {
      if (res.success) {
        this.summary = res.data;
      }
    });

    this.activityNotify.getNotifications().subscribe(
      (res) => {
        if (res.success) {
          this.activities = res.data;
        }
      },
      (reason) => {
        console.log(reason);
      }
    );
  }

  onDeleteActivity(id: number) {
    this.activityNotify.deleteNotification(id).subscribe(
      (res) => {
        if (res.success) {
          this.notifyService.showSuccess(
            "Activity deleted successfully",
            "Success"
          );
        }
      },
      (reason) => {
        this.notifyService.showError(
          "We were unable to delete activity",
          "Operation failed"
        );
      }
    );
  }
}
