import { Component, OnInit } from "@angular/core";
import { IPager } from "src/app/models/IPager.model";
import { Transaction } from "src/app/models/Transaction.model";
import { TransactionService } from "src/app/services/transaction.service";

@Component({
  selector: "app-admin-transactions",
  templateUrl: "./admin-transactions.component.html",
  styleUrls: ["./admin-transactions.component.css"],
})
export class AdminTransactionsComponent implements OnInit {
  private hasMore: boolean;
  private transactions: Transaction[] = [];
  private pager: IPager;
  private searchquery: string;
  constructor(private transactionService: TransactionService) {}

  ngOnInit() {
    this.loadTransactions();
  }

  loadTransactions(page: number = 1, searchquery = "") {
    if (this.pager && page > this.pager.totalPages) return;

    this.transactionService.getTransacts(page, searchquery).subscribe(
      (res) => {
        if (res.success) {
          this.pager = res.data;
          this.transactions = [...this.transactions, ...res.data.results];
          console.log(this.pager);
          this.pager.page = Number(this.pager.page);
          this.hasMore =
            Number(this.pager.page) < Number(this.pager.totalPages);
        }
      },
      (reason) => {
        console.log(reason);
      }
    );
  }

  onSearch() {
    this.transactions = [];
    this.loadTransactions(1, this.searchquery);
  }
}
